
import {Options, Vue} from "vue-class-component";
import {Emit, Prop} from "vue-property-decorator";
import Lixeira from "@/apps/Lixeira.vue";
import MeuComputador from "@/apps/MeuComputador.vue";
import Personalizar from "@/apps/Personalizar.vue";
import InternetExplorer from "@/apps/InternetExplorer.vue";

@Options({
  components: {InternetExplorer, Personalizar, MeuComputador, Lixeira},
  component: {}})
export default class Window extends Vue{

  @Emit("close")
  close(){
    return false;
  }

  @Emit("maximize")
  maximize(){
    return false;
  }

  @Emit("minimaze")
  minimaze(){
    return false;
  }

  dadosPersonalizacao(){
    this.$emit('dadosFromWall')
  }

  @Prop()nomePrograma! : string
  @Prop()iconPrograma! : any

  getImgUrl(pic: string) {
    return require('../assets/'+pic)
  }

  dragElement(elmnt: HTMLElement | null) {
    let pos1 = 0, pos2 = 0, pos3 = 0, pos4 = 0;
    if (document.getElementById(elmnt!.id + "header")) {
      document.getElementById(elmnt!.id + "header")!.onmousedown = dragMouseDown;
    } else {
      elmnt!.onmousedown = dragMouseDown;
    }

    function dragMouseDown(e: any) {
      e = e || window.event;
      e.preventDefault();
      pos3 = e.clientX;
      pos4 = e.clientY;
      document.onmouseup = closeDragElement;
      document.onmousemove = elementDrag;
    }

    function elementDrag(e: any) {
      e = e || window.event;
      e.preventDefault();
      pos1 = pos3 - e.clientX;
      pos2 = pos4 - e.clientY;
      pos3 = e.clientX;
      pos4 = e.clientY;
      elmnt!.style.top = (elmnt!.offsetTop - pos2) + "px";
      elmnt!.style.left = (elmnt!.offsetLeft - pos1) + "px";
    }

    function closeDragElement() {
      document.onmouseup = null;
      document.onmousemove = null;
    }
  }

  mounted(){
    this.dragElement(document.getElementById("mydiv"));
  }

}
